declare const Shopify: any;
import { logWithPrefix } from '../utils/common/common';
import { PreorderApi } from '~common/types';
import { IS_MOCK } from '~index';
import { CartPageController, ProductPageCartController } from './CartPageController';
import { CartPageApiService, CartSettings, MockCartPageApiService } from './services/CartPageApiService';
import { CartData } from './types';

export async function init(preorderApi: PreorderApi, isProductPage?: boolean) {
    const logInit = logWithPrefix('product.init():: ');
    logInit('cart.ts::Shopify object', Shopify);

    const cartPageApiServiceClass = !IS_MOCK
        ? CartPageApiService
        : MockCartPageApiService;
    const cartControllerClass = !isProductPage
        ? CartPageController
        : ProductPageCartController;
    const shopId = window.Shopify.shop;
    
    const cartPageApiService = new cartPageApiServiceClass(
        logWithPrefix(),
        preorderApi,
        shopId,
    );
    
    const cartSettings = await cartPageApiService.getCartSettings()

    const controller = new cartControllerClass(
        logWithPrefix(),
        cartPageApiService,
        cartSettings ??  new CartSettings(),
    );
    controller.init();
}
