import { link } from 'fs';
import pRetry, { FailedAttemptError } from 'p-retry';
import { sleep } from '~../../../libs/utils/async';
import { PreorderApi, PreorderSettingsJson } from '~common/types';
import { IS_MOCK } from '~index';
import {
    isPreviewProductsPage,
    isProductDetailsFromCollectionPage,
    isProductDetailsFromFrontpagePage,
    isProductDetailsPage,
    LogFn,
} from '~utils/common/common';
import { fetchJson } from '~utils/common/fetch';

export class VariantApiJson {
  variant_id: string = '';
  allowed_qnty: number = 0;
}

export class CartSettings {
  forbidding_addition_message: string = 'You cannot add more than ${allowed_qnty} items';
}

export class CartPageApiService {
    constructor(protected log: LogFn, protected preorderApi: PreorderApi, protected shopId: string ) {
        this.log = log.bind(null, CartPageApiService.name, ':: ');
    }
 
    async loadVariantPreorderData(variant_ids: string[]): Promise<VariantApiJson[] | null> {
      try {
        const variants = variant_ids.map(variant_id => `variants[]=${variant_id}`).join("&");
        
        const variantData = await fetchJson<VariantApiJson[]>(
          `${this.preorderApi.url}/storefront/cart/limited-variants?shop=${encodeURIComponent(this.shopId)}&${variants}`,
            {
                retries: 3,
            }
        );
        this.log('loadVariantData:: variantData', variantData);
        
        return variantData?.length ? variantData : null;
      } catch (e) {
        this.log('loadVariantData:: ERROR', e);
        return Promise.resolve(null)
      }
    }
    
    async getCartSettings(): Promise<CartSettings | null> {
      try {
        const settingsData = await fetchJson<CartSettings>(
          `${this.preorderApi.url}/storefront/cart/settings?shop=${encodeURIComponent(this.shopId)}`,
            {
                retries: 3,
            }
        );
        this.log('getCartSettings:: settingsData', settingsData);
        
        return settingsData;
      } catch (e) {
        this.log('getCartSettings:: ERROR', e);
        return Promise.resolve(null)
      }
    }
    
    async changeCart(params: { quantity: string, line: string}): Promise<VariantApiJson | null> {

      try {
        const cartData = await fetchJson<VariantApiJson>(
          `/cart/change.js?${new URLSearchParams(params)}`,
            {
                retries: 3,
            }
        );
        this.log('changeCart:: cartData', cartData);
        
        return  {
          variant_id: '',
          allowed_qnty: 2
        };//variantData;
      } catch (e) {
        this.log('changeCart:: ERROR', e);
        return Promise.resolve(null)
      }
    }

    async loadSettings(shop_id: string, variant_id: string) {
        const options: pRetry.Options = {
            onFailedAttempt: async (error: FailedAttemptError) => {
                await Promise.resolve(
                    this.log('AjaxApi:: settings error', error)
                );
            },
            retries: 3,
        };

        const settings = await fetchJson<PreorderSettingsJson>(
            `${this.preorderApi.url
            }/storefront/products/settings?shop=${encodeURIComponent(
                shop_id
            )}&variant_id=${variant_id}`,
            options
        );

        this.log('loadSettings:: settings', settings);
        return settings;
    }
}

