type CustomProps = { 
  url: string,
  method: string,
  headers: {[key: string]: string},
};
type PatchedXMLHttpRequest = XMLHttpRequest & { __meta_params: CustomProps };
interface GetChangeCartDataReturnType {
  variantInCart: {
      id: number;
      quantity: number;
  } | undefined;
  newVariantQuantity: number;
  preventWhenExceedPreorder?: boolean;
  currentVariantQuantity: number | null;
  newChangeParams: {
      line?: string;
      id?: string;
  };
}

type GetChangeCartDataHandler = (bodyJson: {[key in 'quantity' | 'id' | 'line']: string}, body: BodyInit, cartData: CartData) => Promise<GetChangeCartDataReturnType>;
