    protected async handleCartSubmit(formEl: HTMLFormElement | null, e: Event) {
      if (!formEl) {
        return null;  
      }
      
      let preventSumbit = false;
      
      const cartData: CartData = await fetch('/cart.js').then(r => r.json());
      const cartDataItems: { [key: string]: CartData["items"][0]} = cartData.items.reduce((acc, item) => ({ ...acc, [String(item.id)]: item }), {});
      const cartDataItemIds = Object.keys(cartDataItems);
      const variantPreorderData = await this.cartApiService.loadVariantPreorderData(cartDataItemIds);
      const variantPreorderDataMap: { [key: string]: VariantApiJson } =  variantPreorderData?.reduce((acc, preorderData, i) => ({
        ...acc,
        [preorderData.variant_id]: preorderData,
      }), {}) ?? {};
      
      
      let alertMessage = '';
      const action = formEl.getAttribute("action");
      
      const handleInput = (input: HTMLInputElement | HTMLSelectElement | null, id: string, isUpdate = false) => {
        const inputValue = input ? Number(input.value) : 0;
        const variantInCart = cartDataItems[id];
        const variantInPreorder = variantPreorderDataMap[id];
        if(!isNaN(inputValue) && variantInPreorder){
          const cartValue = isUpdate ? inputValue : ((variantInCart?.quantity ?? 0) + (inputValue || 1));
          
          if(cartValue > variantInPreorder.allowed_qnty) {
            alertMessage += this.getAlertMessage(variantInPreorder.allowed_qnty) + "\r\n";
            
            if (isUpdate && input) {
              input.value = String(variantInPreorder.allowed_qnty);
            } else {
              preventSumbit = true;
            }
          }
        }
      };
      
      
      if (action === '/cart') {
        const inputList = formEl.querySelectorAll<HTMLInputElement>('[name="updates[]"]');
        const isUpdate = true;
        inputList.forEach((input, i) => handleInput(input, cartDataItemIds[i], isUpdate));
      } else if(action === '/cart/add') {
        const formData = new FormData(formEl);
        const formValues = Object.fromEntries(formData.entries());
        const input = formEl.querySelector<HTMLInputElement | HTMLSelectElement>('[name="quantity"]');
        const id = formValues.id as string;
        
        handleInput(input, id);
      }
      
      if(alertMessage) {
        alert(alertMessage);
      }
      // formEl.dispatchEvent(new Event("submit", { 'bubbles': true }));
      // (e as any).detail = { isProcessed: true };
      // // e. = false;
      // formEl.dispatchEvent(e);
      
      if (!preventSumbit) {
        this.isFormProcessed = true;
        formEl.submit();
      }
    }
   
    protected async processBody(body: BodyInit, url: string, headers?:  CustomProps["headers"]): Promise<{ newBody: BodyInit | null, isChanged: boolean, variantPreorderData: VariantApiJson | null }> {
      let newBody: BodyInit | null = body;
      let variantPreorderData: VariantApiJson | null = null;
      let isChanged = false;
      const controller = this;
      
      for (const [urlRegExp, getChangeCartData] of this.urlHandlers.entries()) {
        const isCartChangeRequest = urlRegExp.test(url);
        
        if(isCartChangeRequest) {
          const cartData: CartData = await fetch('/cart.js').then(r => r.json());
          let bodyJson = tryParseJsonString(body as string);
          let isJsonString = false;
          
          if (bodyJson !== undefined) {
            isJsonString = true;
          }
          
          if(typeof body === "string") {
             const contentType = headers?.["Content-Type"] ?? '';
             
             if (contentType.includes("application/x-www-form-urlencoded")) {
                body = new URLSearchParams(body);
             }
          }
          
          if(body instanceof FormData || body instanceof URLSearchParams) {
            bodyJson = Object.fromEntries(body.entries()) as { id: string};
          }
          
          const { variantInCart, newVariantQuantity, currentVariantQuantity, newChangeParams, preventWhenExceedPreorder } =  await getChangeCartData(bodyJson, body, cartData);
          
            
          // if (variantInCart) {
            const variantPreorderDataResults = await this.cartApiService.loadVariantPreorderData([String(variantInCart?.id || bodyJson.id)]);
            variantPreorderData = variantPreorderDataResults ? variantPreorderDataResults[0] : null;

            if (variantPreorderData && currentVariantQuantity !== null) {
              if (newVariantQuantity > variantPreorderData.allowed_qnty) {
                const newBodyJson = {
                  ...bodyJson,
                  quantity: String(variantPreorderData.allowed_qnty),
                  ...newChangeParams,
                };

                
                if(preventWhenExceedPreorder) {
                  newBody = null;
                } else {
                  newBody = isJsonString ? JSON.stringify(newBodyJson) : new URLSearchParams(newBodyJson).toString();
                }
                
                const alertMessage = this.getAlertMessage(variantPreorderData.allowed_qnty);
                alert(alertMessage);

              }
            }
          // }
          isChanged = true;
        }
      }
       
      
      return { newBody, isChanged, variantPreorderData };
    }
    
    getAlertMessage(quantity: number) {
      return this.cartSettings.forbidding_addition_message.replace('${allowed_qnty}', String(quantity));
    }
    
    protected patchCartChangeRequest() {
      const controller = this;
      
      (function (originalSend) {
        XMLHttpRequest.prototype.send = async function (body: BodyInit) {
          const xhr: PatchedXMLHttpRequest = this as any;
          console.log('send start -> context: ', this); 
          xhr.addEventListener('loadend', () => {
            debugger;
            controller.isRequestProcessed = false;
           });
          
          const { newBody, isChanged } = await controller.processBody(body, xhr.__meta_params?.url, xhr.__meta_params?.headers);
          
          if (!isChanged || newBody) {
            controller.isRequestProcessed = true;
            originalSend.call(xhr, newBody);
          }
          
        };
      })(XMLHttpRequest.prototype.send);
    
      (function (originalOpen) { 
          type ajaxT = typeof XMLHttpRequest;
          type ArgsType = Parameters<(ajaxT)["prototype"]["open"]>;
          XMLHttpRequest.prototype.open = function () {
            const args = arguments as unknown as ArgsType;
            const xhr: PatchedXMLHttpRequest = this as any;
            
            xhr.__meta_params = xhr.__meta_params ?? {};
            
            xhr.__meta_params = {
              ...xhr.__meta_params,
              method: args[0],
              url: args[1],
            };
            
            
            originalOpen.call(xhr, ...args);
          };
      })(XMLHttpRequest.prototype.open);
      
      (function (originalSetRequestHeader) { 
          type ajaxT = typeof XMLHttpRequest;
          type ArgsType = Parameters<(ajaxT)["prototype"]["setRequestHeader"]>;
          XMLHttpRequest.prototype.setRequestHeader = function () {
            const args = arguments as unknown as ArgsType;
            const xhr: PatchedXMLHttpRequest = this as any;
            
            xhr.__meta_params = xhr.__meta_params ?? { headers: {} };
            
            xhr.__meta_params.headers = {
              ...xhr.__meta_params.headers,
               [args[0]]: args[1],
            };
            
            
            originalSetRequestHeader.call(xhr, ...args);
          };
      })(XMLHttpRequest.prototype.setRequestHeader);    
      
      
      (function (originalFetch) {
          type ArgsType = Parameters<typeof window.fetch>;
          window.fetch = async function (...args: ArgsType) {
            
            const [ requestInfo, requestInit]  = args;
            let url = requestInfo as string;
            let body = requestInit?.body;
            
            if (requestInfo instanceof Request) {
              url = requestInfo.url;
            }
            else if (typeof requestInfo !== 'string') {
              url = String(requestInfo);
            }
            
            if (body) {
              
              const { newBody, isChanged, variantPreorderData } = await controller.processBody(body, url);
              if (newBody !== body) {
                requestInit!.body = newBody;
              }
              
              if (requestInit!.body) {
                controller.isRequestProcessed = true;
                return originalFetch(...args).then(r => {
                  controller.isRequestProcessed = false;
                  return r;
                });
               } else {
                
                 return Promise.reject({
                   json: () => Promise.resolve({
                      "status": 422,
                      "message": "Cart Error",
                      // "description": "You can't add more to the cart."
                   })
                 })
               }
            }
            
            return originalFetch(...args);
          };
      })(window.fetch);
      
    }

