    static async getChangeCartData(changeParams: { quantity: string, line: string}, body: BodyInit, cartData: CartData): Promise<GetChangeCartDataReturnType> {
        const variantIndex = Number(changeParams.line) - 1;
        const newVariantQuantity = Number(changeParams.quantity);
        
        console.log('cartData', cartData);
        const variant = cartData.items[variantIndex];
        const currentVariantQuantity = variant?.quantity ?? 0;

        return { variantInCart: variant, newVariantQuantity, currentVariantQuantity, newChangeParams: { line: changeParams.line, } };
    }    
    
